const Label = styled.label({
  color: theme.colors.grey_200,
})

const copyToClipboard = str => {
  const el = document.createElement('textarea')
  el.value = str
  document.body.appendChild(el)
  el.select()
  document.execCommand('copy')
  document.body.removeChild(el)
}

const formatCode = (value, type) => {
  const code = `(function() {
    var data = ${value};
    var script = document.createElement('script');
    script.type = 'application/ld+json';
    script.innerHTML = JSON.stringify(data);
    document.head.appendChild(script);
  })();`

  switch (type) {
    case 'prettified':
      const formattedCode = prettier.format(code, {
        parser: 'babel',
        plugins: [parserBabylon],
      })
      return `<script>\n${formattedCode}</script>`
    case 'minified':
      const { code: minifiedCode } = UglifyJS.minify(code)
      return `<script>${minifiedCode}</script>`
  }
}

const Form = () => {
  const [input, setInput] = useState('')
  const [output, setOutput] = useState('')
  const [outputType, setOutputType] = useState('prettified')
  const [error, setError] = useState(false)
  const formattedOutput = output && !error ? formatCode(output, outputType) : ''

  return (
    <div
      css={{
        display: 'grid',
        gridTemplateColumns: '1fr',
        gridGap: rem(20),
        padding: rem(20),
        [media.tabletLg]: {
          gridTemplateColumns: '1fr 1fr',
          gridGap: rem(50),
          padding: rem(50),
        },
        background: '#fff',
        borderRadius: 8,
        boxShadow: '0 5px 30px #CBD6D9',
      }}
    >
      <div>
        <Label htmlFor="input-code">JSON-LD input</Label>
        <Space y={10} />
        <TextArea
          id="input-code"
          autosize={{ minRows: 8 }}
          value={input}
          onChange={e => setInput(e.target.value)}
        />
        <Space y={25} />
        {error && (
          <Fragment>
            <Alert message="Please enter valid JSON code" type="error" />
            <Space y={25} />
          </Fragment>
        )}
        <Button
          type="primary"
          size="large"
          onClick={() => {
            setError(false)

            if (!input) setError(true)

            try {
              JSON.parse(input)
            } catch {
              setError(true)
              return
            }

            setOutput(input)
          }}
        >
          Generate Tag Manager Code
        </Button>
      </div>
      <div>
        <Label htmlFor="output-code">Javascript output</Label>
        <Space y={10} />
        <TextArea
          id="output-code"
          autosize={{ minRows: 8 }}
          value={formattedOutput}
          disabled={!output}
          // css={{ '&[disabled]': { backgroundColor: theme.colors.lightBlue } }}
        />
        <Space y={25} />
        <div
          css={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginTop: -10,
            marginLeft: -10,
            '> *': {
              marginTop: 10,
              marginLeft: 10,
            },
          }}
        >
          <Button
            size="large"
            disabled={!output}
            onClick={() => copyToClipboard(formattedOutput)}
          >
            Copy Tag Manager Code
          </Button>
          <Radio.Group
            value={outputType}
            onChange={e => setOutputType(e.target.value)}
            size="large"
            disabled={!output}
          >
            <Radio.Button value="prettified">Prettified</Radio.Button>
            <Radio.Button value="minified">Minified</Radio.Button>
          </Radio.Group>
        </div>
      </div>
    </div>
  )
}

